$crait-color : #c0392b;
$background-color : #faebd745;

nav {
    width: 100%;
    margin: 20px;
    padding: 20px;
    background: $background-color;
    border: 2px solid $crait-color;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    font-family: 'Roboto', sans-serif;
}

.nav-links {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 50%;
    .nav {
        text-decoration: none;
        color: white;
        font-weight: bold;
        border: 2px solid $crait-color;
        padding: 10px;
        border-radius: 10px;
        font-family: 'Share Tech Mono', monospace;
        font-size: 20px;
        &:hover { 
            background-color: #EAFCEB;
            cursor: pointer;
            box-shadow: 
                0 0 20px 5px #EAFCEB,  /* inner white */
                0 0 30px 10px #337F23, /* middle magenta */
                0 0 50px 20px #69A267; /* outer cyan */
            border: none;
            color: black;
          }
    }
}

.user-image {
  height: 5em;
  width: 5em;
  border-radius: 50%;
}

.user-info {
  display: flex;
  width: 40%;
  // justify-content: space-around;
}

.user-section {
  margin: 10px;
  h2 {
    font-family: 'Share Tech Mono', monospace;
    color: white;
    font-weight: bold;
  }
}
